.split {
  float: left;
  color: white;
  font-style: normal;
  font-size: 14px;
  line-height: 54px;
  opacity: 0.8;
}
.split-1 {
  float: left;
  color: white;
  font-style: normal;
  font-size: 20px;
  line-height: 52px;
  margin: 0 10px;
  opacity: 0.8;
}
.login {
  opacity: 0.8;
}
.head {
  background-color: #2c2f33;
  height: 52px;
  overflow: hidden;
}
.head .head-main {
  width: 1000px;
  margin: 0 auto;
  overflow: hidden;
  /* font-family: PingFangSC-Regular; */
}
.head .logo {
  text-indent: -9999em;
  display: block;
  width: 165px;
  height: 20px;
  margin-top: 16px;
  float: left;
}
.head .head-menu {
  width: 100%;
  float: right;
}
.head .head-menu li {
  list-style: none;
  display: inline;
  position: relative;
  z-index: 2;
}
.head .head-menu li a {
  font-size: 14px;
  color: #fff;
  opacity: 0.8;
  filter: alpha(opacity=80);
  line-height: 52px;
  padding: 10px 15px;
  margin-left: 4px;
}
.head .head-menu li.line {
  /*border-right: 1px solid #fff; */
}
/*.head .head-menu li.current a,.head .head-menu li a:hover{  background-color: #FA7F02; text-decoration: none;}*/
.head .head-menu li.back {
  position: absolute;
  height: 52px;
  background-color: #fa7f02;
  z-index: 1;
}

.head .login {
  width: 340px;
  float: right;
  line-height: 52px;
  color: #fff;
  text-align: right;
}
.head .login a {
  color: #fff;
}
.head .login .user-name {
  max-width: 136px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
}
.head .head-menu .current-cat a {
  color: #f37327;
  opacity: 1;
  filter: alpha(opacity=100);
  /* font-family: PingFangSC-Regular; */
}
#menu li a:hover {
  color: #f37327;
  opacity: 1;
  filter: alpha(opacity=100);
}

